@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
.expenses-filter {
    color: white;
    padding: 0 1rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }
.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #2a2a2a;
  color: white;
  border-radius: 12px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: .5s all;
}
  
.expense-date:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
  
.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}
  
.expense-date__year {
  font-size: 0.75rem;
}
  
.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}
.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;
  margin: 1rem auto;
  width: 99%;
  border-radius: 12px;
  background-color: #4b4b4b;
}
  
.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}
  
.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: white;
}
  
.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}
  
@media screen and (min-width: 580px) {
  .expense-item {
    width: 90%;
  }
    
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }
  
  .expense-item__description h2 {
    font-size: 1.25rem;
  }
  
  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}
.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #f8dfff;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
  width: 90%;
}

.Chart-component{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.total-expenses{
  height: 10rem;
  overflow: auto;
  background-color: #40005d;
  padding: 1rem;
  margin: 1rem;
  border-radius: 12px;
  line-height: 10px;
  border: 3px solid #f8dfff;
  min-width: 150px;
}

.total-expenses p {
  font-size: 1rem;
  color: #f8dfff;
}

/* ScrollBar styling */
.total-expenses::-webkit-scrollbar {
  width: 2px;
}

.total-expenses::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 10px;
  margin: 3px 0px;
}

.total-expenses::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: yellow;
}

/* Media Query */
@media screen and (min-width: 500px){
  .Chart-component{
    flex-direction: row;
    justify-content: center;
  }

  .chart{
    width: 70%
  }

}
  
.expense {
    padding: 1rem;
    background-color: rgb(31, 31, 31);
    border-radius: 2%;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
  }

  .no-expense__paragraph{
    color: white;
    font-size: 12px;
    letter-spacing: 1px;
    -webkit-animation: no_expense 1s infinite;
            animation: no_expense 1s infinite;
  }

  @-webkit-keyframes no_expense {
    0%   {opacity: 0;}
    25%  {opacity: 0.5;}
    50%  {opacity: 1;}
    75%  {opacity: 0.5;}
    100% {opacity: 0;}
  }

  @keyframes no_expense {
    0%   {opacity: 0;}
    25%  {opacity: 0.5;}
    50%  {opacity: 1;}
    75%  {opacity: 0.5;}
    100% {opacity: 0;}
  }
.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}
  
.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}
  
.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}
  
.new-expense__actions {
  text-align: right;
}
.new-expense {
  background-color: #a892ee;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
  
.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}
  
.new-expense button:hover,
.new-expense button:active {
  background-color: #510674;
  border-color: #510674;
}
  
.new-expense button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}
  
.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #ddb3f8;
}
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}
